import request from '@/utils/request'
const groupName = 'serviceType'
export default {
  /**
   * 
   */
   getByPage(page) {
    return request({
      url: `/${groupName}/getByPage`,
      method: 'post',
      data: page
    })
  },
  /**
   * 
   */
   enableById(id) {
    return request({
      url: `/${groupName}/enableById/${id}`,
      method: 'put'
    })
  },
  /**
   * 
   */
  disableById(id) {
    return request({
      url: `/${groupName}/disableById/${id}`,
      method: 'put'
    })
  },
  /**
   * 
   */
  deleteById(id) {
    return request({
      url: `/${groupName}/delete/${id}`,
      method: 'delete'
    })
  },
  /**
   * id
   */
  get(id) {
    return request({
      url: `/${groupName}/get/${id}`,
      method: 'get'
    })
  },
  /**
   * ,
   */
  update(lwServiceType){
    return request({
      url: `/${groupName}/update`,
      method: 'put',
      data: lwServiceType 
    })
  },
  /**
   * 
   */
   save(lwServiceType){
    return request({
      url:`/${groupName}/save`,
      method:'post',
      data:lwServiceType
    })
  },
  /**
   * 
   */
   getAll(){
    return request({
      url:`/${groupName}/getAll`,
      method:'get'
    })
  },
}
