import request from '@/utils/request'
const groupName = 'user'
export default {
  /**
   * 
   */
  getByPage(page) {
    return request({
      url: `/${groupName}/getByPage`,
      method: 'post',
      data: page
    })
  },
  /**
   * 
   */
  enableById(id) {
    return request({
      url: `/${groupName}/enableById/${id}`,
      method: 'put'
    })
  },
  /**
   * 
   */
  disableById(id) {
    return request({
      url: `/${groupName}/disableById/${id}`,
      method: 'put'
    })
  },
  /**
   * 
   */
  deleteById(id) {
    return request({
      url: `/${groupName}/delete/${id}`,
      method: 'delete'
    })
  },
  /**
   * id
   */
  get(id) {
    return request({
      url: `/${groupName}/get/${id}`,
      method: 'get'
    })
  },
  /**
   * id
   */
   getUpdateInfo(id) {
    return request({
      url: `/${groupName}/getUpdateInfo/${id}`,
      method: 'get'
    })
  },
  /**
   * ,
   */
  update(lwUser){
    return request({
      url: `/${groupName}/update`,
      method: 'put',
      data: lwUser 
    })
  },
  /**
   * 
   */
   getReviewerList(serviceNo) {
    return request({
      url: `/${groupName}/getReviewerList/${serviceNo}`,
      method: 'get'
    })
  },
}
